import React, { useState, useEffect } from 'react';

const AuctionListing = ({ propertyId }) => {
  const [auction, setAuction] = useState(null);
  const [bidAmount, setBidAmount] = useState('');

  useEffect(() => {
    // Simulating fetching auction details
    setAuction({
      seller: '0x1234...5678',
      startingPrice: '10',
      endTime: new Date(Date.now() + 86400000).toISOString(),
      highestBid: '12',
      highestBidder: '0x5678...9012',
    });
  }, [propertyId]);

  const handleBid = (e) => {
    e.preventDefault();
    console.log(`Placing bid of ${bidAmount} EACL for property ${propertyId}`);
    alert(`Bid of ${bidAmount} EACL placed successfully!`);
    setBidAmount('');
  };

  if (!auction) {
    return <div>Loading auction details...</div>;
  }

  return (
    <div>
      <h2>Auction for Property #{propertyId}</h2>
      <p>Seller: {auction.seller}</p>
      <p>Starting Price: {auction.startingPrice} EACL</p>
      <p>End Time: {new Date(auction.endTime).toLocaleString()}</p>
      <p>Highest Bid: {auction.highestBid} EACL</p>
      <p>Highest Bidder: {auction.highestBidder}</p>
      
      <form onSubmit={handleBid}>
        <input
          type="text"
          value={bidAmount}
          onChange={(e) => setBidAmount(e.target.value)}
          placeholder="Enter bid amount"
        />
        <button type="submit">Place Bid</button>
      </form>
    </div>
  );
};

export default AuctionListing;